// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios'; 
import Loading from 'vue-loading-overlay'; 
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap';
import VeeValidate  from 'vee-validate';
import zhTWvalidate from 'vee-validate/dist/locale/zh_TW';

import App from './App';
import router from './router';
import './bus';
import currencyFilter from './components/filters/currency';
import timeFilter from './components/filters/timefilter';

Vue.config.productionTip = false;
Vue.use(VueAxios, axios); 
Vue.use(VeeValidate, {        // 表格驗證
  events: 'input|blur',
});
VeeValidate.Validator.localize('zh_TW',zhTWvalidate);   // 將驗證の插件改成中文版

Vue.component('Loading',Loading);  // 全域宣告使用 Loading 原件
Vue.filter('currency',currencyFilter);  // 顯示錢的逗號 filter
Vue.filter('timefilter',timeFilter);  // 時間轉換 filter

axios.defaults.withCredentials = true ;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});
router.beforeEach((to, from, next) => {
  // ...
  console.log('to',to ,'from' ,from ,'next',next);
  if(to.meta.requiresAuth){
    const api =`${process.env.API_PATH}/api/user/check`; //'https://vue-course-api.hexschool.io/api/class90431/products';
    axios.post(api).then((response) => {
      console.log(response.data);
      if(response.data.success){
        next();
      }else{
        next({
          path:'/login'
        })
      };
    });
  }else{
    next();
  };
});


