<template>
  <div>　  
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
      <div class="col-md-4 mb-4" v-for="(item, index) in products" :key="index">
        <div class="card border-0 shadow-sm">
          <div style="height: 150px; background-size: cover; background-position: center"
            :style="{backgroundImage:`url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <!-- <div class="h5">2,800 元</div> -->
              <del class="h5" v-if="!item.price">原價{{item.origin_price}}元</del>
              <del class="h6" v-if="item.price">原價{{item.origin_price}}元</del>
              <div class="h5" v-if="item.price">現在只要 {{ item.price }} 元</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm" @click.prevent="getSingleProduct(item)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem == item.id"></i>
              查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click.prevent="addtoCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem == item.id"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- 分頁元件 -->
    <Pagination :pageComponent="pagination" v-on:turnpage="getProducts"></Pagination>
    <!-- 購物車表格 -->
    <table class="table table-striped">
      <thead>
        <tr>
          <th width="80" scope="col">#</th>
          <th scope="col">品名</th>
          <th scope="col">數量</th>
          <th width="120" scope="col">單價</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in carts" :key="index">
          <td>
            <button type="button" class="btn btn-outline-danger btn-sm" @click.prevent="removeCartItem(item.id)">
              <i class="far fa-trash-alt" ></i>
            </button>
          </td>
          <td>{{ item.product.title }}</td>
          <td>{{ item.qty }} / {{ item.product.unit }}</td>
          <td class="text-right">{{ item.total }}</td>
        </tr> 
        <tr class="bg-info text-white text-right">
          <td></td>
          <td></td>
          <td v-if="carts.total == carts.finaltotal">總價</td>
          <td v-else><del class="text-white">總價</del></td>
          <td v-if="carts.total == carts.finaltotal" class="text-right">{{ carts.total }}</td>
          <td v-else class="text-right"><del class="text-white">{{ carts.total }}</del></td>
        </tr>
        <tr class="text-right" v-if="carts.total !== carts.finaltotal">
          <td></td>
          <td></td>
          <td class="text-success"><strong>折扣價</strong></td>
          <td class="text-success text-right"><strong>{{ carts.finaltotal }}</strong></td>
        </tr>
      </tbody>
    </table>
      <!-- 優惠碼 Input -->
    <div class="input-group mb-3">
      <input v-model="coupon_code" type="text" class="form-control" placeholder="請輸入優惠代碼">
      <div class="input-group-append">
        <button class="btn btn-outline-secondary" type="button" @click.prevent="addCouponCode">套用優惠代碼</button>
      </div>
    </div>
    <!-- 收件人資料 Form -->
    <form class="mt-5" @submit.prevent="creatOrder">
      <div class="form-group">
        <label for="useremail">Email</label>
        <input type="text" v-model="form.user.email" 
          v-validate="'required|email'" :class="{'is-invalid':errors.has('email')}"
          name="email" id="useremail" class="form-control"  placeholder="請輸入 Email">
        <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
      </div>
      <div class="form-group">
        <label for="username">收件人姓名</label>
        <input type="text" v-model="form.user.name" v-validate="'required'" 
        :class="{'is-invalid':errors.has('name')}"
        name="name" id="username" class="form-control" placeholder="請輸入姓名">
        <span class="text-danger" v-if="errors.has('name')">請輸入姓名</span>
      </div>
      <div class="form-group">
        <label for="userphone">收件人電話</label>
        <input type="text" v-model="form.user.tel" v-validate="'required'" 
        :class="{'is-invalid':errors.has('phone')}"
        name="phone" id="userphone" class="form-control" placeholder="請輸入電話">
        <span class="text-danger" v-if="errors.has('phone')">請輸入電話</span>
      </div>
      <div class="form-group">
        <label for="useradress">收件人地址</label>
        <input type="text" v-model="form.user.address" v-validate="'required'" 
        :class="{'is-invalid':errors.has('address')}"
        name="address" id="useradress" class="form-control" placeholder="請輸入地址">
        <span class="text-danger" v-if="errors.has('address')">請輸入地址</span>
      </div>
      <div class="form-group">
        <label for="usermessage">留言</label>
        <textarea class="form-control" v-model="form.message" name="" id="usermessage"  rows="3"></textarea>
      </div>
      <div class="text-right">
        <button type="submit" class="btn btn-danger mb-2" >送出</button>　
      </div>
    </form>
    <!-- 單一商品 Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="">
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
              <del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
              <div class="h4" v-if="product.price">現在只要 {{ product.price }} 元</div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">
                選購 {{num}} {{product.unit}}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計 <strong>{{ product.num * product.price }}</strong> 元
            </div>
            <button type="button" class="btn btn-primary" @click="addtoCart(product.id,product.num)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem == product.id"></i>
              <!-- v-if="product.id === status.loadingItem" -->
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Pagination from '../Pagination';
  import $ from 'jquery';

  export default {
    data() {
      return {
        products: [],
        product: {},
        isLoading: false,
        pagination: {},
        status:{
          loadingItem:''
        },
        carts:{},
        coupon_code:'',
        form:{
          user:{
            name:'',
            email:'',
            tel:'',
            address:''
          },
          message:''
        },
      }
    },
    methods: {
      getProducts(page = 1) {
        const vm = this;
        const url = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/products?page=${page}`;
        vm.isLoading = true;
        this.$http.get(url).then((response) => {
          // console.log(response);
          vm.products = response.data.products;
          // console.log(vm.products);
          vm.pagination = response.data.pagination;
          vm.isLoading = false;
        });
      },
      getSingleProduct(item) {
        // console.log(id)
        const vm = this;
        const url = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/product/${item.id}`;
        vm.status.loadingItem = item.id;
        this.$http.get(url).then((response) => {
          console.log(response);
          vm.product = response.data.product; 
          vm.product.num = 1;      //  將 product.num 預設為數值 1  
          // console.log(vm.product.num);
          $('#productModal').modal('show');
          vm.status.loadingItem = '';
        });
      },
      addtoCart(id,qty = 1){  // qty = 產品數量
        // console.log(id,qty);
        // $('#productModal').modal('hide');
        const vm = this;
        const url = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/cart`;
        const cart = {
          product_id:id,
          qty
        };
        vm.status.loadingItem = id;
        this.$http.post( url, {data:cart} ).then((response) => {    
          // console.log(response);
          vm.status.loadingItem = '';
          vm.$bus.$emit('messsage:push',response.data.message,'success');  // Alert： ‘已加入購物車‘
          vm.getCart();
          $('#productModal').modal('hide');       //  關閉 Modal 
        });
      },
      getCart(){
        const vm = this;
        const url = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/cart`;
        vm.isLoading = true;
        this.$http.get(url).then((response) => {
          console.log(response); 
          vm.isLoading = false;
          vm.carts = response.data.data.carts;
          vm.carts.total = response.data.data.total;
          vm.carts.finaltotal = response.data.data.final_total;
          // console.log(vm.carts,vm.carts.total);
        });
      },
      removeCartItem(id){
        // console.log(id);
        const vm = this;
        const url = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/cart/${id}`;
        vm.isLoading = true;
        this.$http.delete(url).then((response) => {
          console.log(response); 
          vm.isLoading = false;    
          vm.getCart();
          vm.$bus.$emit('messsage:push',response.data.message,'success');   // Alert： ‘已刪除‘
        });
      },
      addCouponCode(){
        const vm = this;
        const url = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/coupon`;
        const coupon = {
          code:vm.coupon_code
        };
        // vm.isLoading = true;
        this.$http.post( url , {data:coupon} ).then((response) => {
          console.log(response); 
          vm.getCart();
          vm.coupon_code ='';
          // vm.isLoading = false;     
          // vm.$bus.$emit('messsage:push',response.data.message,'success');   // Alert： ‘已刪除‘
        }); 
      },
      creatOrder(){
        const vm = this;
        const url = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/order`;
        const order = vm.form
        // vm.isLoading = true;
        this.$validator.validate().then(valid => {
          if (valid) {
            this.$http.post( url , {data:order} ).then((response) => {
            console.log('訂單已建立',response); 
            // vm.isLoading = false;     
              if(response.data.message === "購物車內無資料"){
                vm.$bus.$emit('messsage:push',response.data.message,'danger');
              }else{
                vm.$bus.$emit('messsage:push',response.data.message,'success');
                vm.$router.push(`customer_checkout/${response.data.orderId}`)
              };
            });
          } else {
            console.log('欄位不完整'); 
          };
        }); 
      }
    }, 
    components: {
      Pagination
    },
    created() {
      this.getProducts();
      this.getCart();
    }
  }

</script>



