<template> 
  <div>
    <loading :active.sync="isLoading"></loading> 
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120">購買時間</th>
          <th>Email</th>
          <th  >購買款項</th>
          <th width="120">應付金額</th>
          <th width="100">是否付款</th> 
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in orders" :key="index" 
          :class="{'table-danger': !item.is_paid,'table-success':item.is_paid}">
          <td>{{ item.create_at | timefilter }}</td>
          <td>{{ item.user.email }}</td> 
          <td>
            <ul class="list-unstyled">
              <li v-for="(product, i) in item.products" :key="i">
                {{ product.product.title }} 數量：{{ product.qty }}
                {{ product.product.unit }}
              </li>
            </ul>
          </td>
          <td class="text-right">
            {{ item.total  | currency }}
          </td>
          <td>
            <strong v-if="item.is_paid" class="text-success">已付款</strong>
            <span v-else class="text-danger">未付款</span>
          </td> 
        </tr>
      </tbody>
    </table>
    <!-- 放’分頁元件‘的地方 -->
    <Pagination :pageComponent="pagination" v-on:turnpage="getOrders"></Pagination>  
  </div>
</template>

<script> 
  import Pagination from '../Pagination.vue';

  export default {
    data() {
      return { 
        pagination:{},  
        isLoading:false, 
        orders:[],
      }
    },
    methods: {
      getOrders(page = 1){
        let api =`${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/orders?page=${page}`; //'https://vue-course-api.hexschool.io/api/class90431/products';
        const vm = this;
        vm.isLoading = true ;
        this.$http.get(api).then((response) => {
          console.log(response.data); 
          vm.pagination = response.data.pagination;
          vm.isLoading = false;
          vm.orders = response.data.orders;
          console.log(vm.orders[0].products); 
        });      
      }
    },
    components:{
      Pagination
    },
    created() {
      this.getOrders(); 
    }
  }

</script> 



