<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)">建立新的產品
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120">分類</th>
          <th>名稱</th>
          <th width="120">原價</th>
          <th width="120">售價</th>
          <th width="100">是否啟用</th>
          <th width="120">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in products" :key="index">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">
            {{ item.origin_price | currency }}
          </td>
          <td class="text-right">
            {{ item.price  | currency }}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" 
              @click="openModal(false , item)">編輯
            </button>
            <button class="btn btn-outline-danger btn-sm" 
              @click="deleteModal(item)">刪除
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- 放’分頁元件‘的地方 -->
    <Pagination :pageComponent="pagination" v-on:turnpage="getProducts"></Pagination>
    <!-- Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image">輸入圖片網址</label>
                  <input type="text" class="form-control"
                  v-model="tempProduct.imageUrl"
                  id="image" placeholder="請輸入圖片連結">
                </div>
                <div class="form-group">
                  <label for="customFile">或 上傳圖片
                    <i class="fas fa-circle-notch fa-spin" v-if="status.fileUploading"></i>
                    <!-- <i class="fas fa-spinner fa-spin"></i> -->
                  </label>
                  <input type="file" id="customFile" class="form-control" 
                    ref="files" @change="uploadFile">
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid" :src="tempProduct.imageUrl" alt="">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title">標題</label>
                  <input type="text" class="form-control"
                  v-model="tempProduct.title"
                  id="title" placeholder="請輸入標題">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category">分類</label>
                    <input type="text" class="form-control"
                    v-model="tempProduct.category"
                    id="category" placeholder="請輸入分類">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">單位</label>
                    <input type="unit" class="form-control" 
                    v-model="tempProduct.unit"
                    id="unit" placeholder="請輸入單位">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price">原價</label>
                    <input type="number" class="form-control"
                    v-model="tempProduct.origin_price"
                    id="origin_price" placeholder="請輸入原價">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">售價</label>
                    <input type="number" class="form-control" 
                    v-model="tempProduct.price"
                    id="price" placeholder="請輸入售價">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description">產品描述</label>
                  <textarea type="text" class="form-control" 
                  v-model="tempProduct.description"
                  id="dription" placeholder="請輸入產品描述"></textarea>
                </div>
                <div class="form-group">
                  <label for="content">說明內容</label>
                  <textarea type="text" class="form-control" 
                  v-model="tempProduct.content"
                  id="content" placeholder="請輸入產品說明內容"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" 
                    v-model="tempProduct.is_enabled"
                    :true-value="1"
                    :false-value="0"
                    type="checkbox" id="is_enabled">
                    <label class="form-check-label" for="is_enabled">
                      是否啟用
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
          </div>
        </div>
      </div>
    </div>
    <!-- 刪除 Modal -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="deleteProduct">確認刪除</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery';
  import Pagination from '../Pagination';

  export default {
    data() {
      return {
        products : [],
        pagination:{},
        tempProduct : {},
        isNew : false,
        isDelete : false ,
        tempApi : "" ,
        isLoading:false,
        status:{
          fileUploading:false,
        },
      }
    },
    methods: {
      getProducts( page = 1 ) {
        let api =`${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/admin/products?page=${page}`; //'https://vue-course-api.hexschool.io/api/class90431/products';
        const vm = this;
        vm.isLoading = true ;
        this.$http.get(api).then((response) => {
          console.log(response.data);
          vm.isLoading = false;
          vm.products = response.data.products;
          vm.pagination = response.data.pagination;
        });
      },
      openModal(isNew , item) {
        if(isNew){
          this.tempProduct = {};
          this.isNew = true;
        }else{
          this.tempProduct = Object.assign({},item); 
          this.isNew = false;
        };
        $('#productModal').modal('show');
      },
      updateProduct(){
        let api =`${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/admin/product`; //'https://vue-course-api.hexschool.io/api/class90431/products';
        let httpMethod = 'post';
        const vm = this;
        if(!vm.isNew){
          api = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/admin/product/${vm.tempProduct.id}`;
          httpMethod = 'put';
        };
        this.$http[httpMethod]( api , {data:vm.tempProduct} ).then((response) => {
          console.log(response.data);
          if(response.data.success){
            $('#productModal').modal('hide');  
            vm.$bus.$emit('messsage:push',response.data.message,'success'); // Alert: 已建立新產品
            vm.getProducts(vm.pagination.current_page); // ’編輯完成‘後 跟新頁面至 ‘當前頁面’ 
          }else{
            console.log('新增失敗'); 
          };
          // vm.products = response.data.products;
        });
      },
      deleteModal(item){
        this.tempProduct = item;
        console.log(this.tempProduct);
        let api = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/admin/product/${item.id}`;
        this.tempApi = api;
        // console.log(api,this.products,item.id,this.tempApi);
        this.isDelete = false ;
        $('#delProductModal').modal('show');
      },
      deleteProduct(){
        const vm = this ;
        this.isDelete = true ;  
        if(this.isDelete){
          this.$http.delete(vm.tempApi).then((response) => {
          console.log(response.data);
          $('#delProductModal').modal('hide');
          this.$bus.$emit('messsage:push',response.data.message,'danger'); // Alert: 已刪除產品
          vm.getProducts(); 
          }); 
        }
      },
      uploadFile(){
        console.log(this);
        const uploadedFile = this.$refs.files.files[0];
        const vm = this;
        const formData = new FormData();
        formData.append('file-to-upload' , uploadedFile);
        const url = `${process.env.API_PATH}/api/${process.env.CUSTOMER_PATH}/admin/upload`;
        vm.status.fileUploading = true;
        this.$http.post( url , formData , {
          headers:{
            'Content-Type':'multipart/form-data'
          }
        }).then((response)=>{
          console.log(response.data);
          vm.status.fileUploading = false;
          if (response.data.success) {
            // vm.tempProduct.imageUrl = response.data.imageUrl;
            // console.log(vm.tempProduct);
            vm.$set( vm.tempProduct , 'imageUrl' , response.data.imageUrl );
            this.$bus.$emit('messsage:push','上傳成功','success'); // Alert: 上傳成功
            // console.log(response.data);
          }else{
            this.$bus.$emit('messsage:push','上傳失敗','danger');
          }
        });
      }
    },
    components:{
      Pagination
    },
    created() {
      this.getProducts();
    }
  }

</script> 



