<template>
  <div>
    <nav aria-label="Page navigation example">
      <ul class="pagination justify-content-center">
        <li class="page-item" :class="{ 'disabled':!pageComponent.has_pre }">
          <a class="page-link" href="#" @click.prevent="turnPage(pageComponent.current_page - 1)" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="(page, index) in pageComponent.total_pages" :key="index"
          :class="{'active': pageComponent.current_page == page}">
          <a class="page-link" href="#" @click.prevent="turnPage(page)">{{ page }}</a>
        </li> 
        <li class="page-item" :class="{ 'disabled':!pageComponent.has_next }">
          <a class="page-link" href="#" @click.prevent="turnPage(pageComponent.current_page + 1)" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  props:['pageComponent'],
  methods:{
    turnPage(page){
      this.$emit('turnpage',page);
    }
  }
}
</script>


